import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import Rating from "./Rating";
import Pagination from "./pagination";
import {useDispatch, useSelector } from "react-redux";
import { listProduct} from "../../Redux/Actions/ProductActions";
import Loading from "../LoadingError/Loading";
import Message from "../LoadingError/Error";
import { Carousel } from 'react-responsive-carousel';
import "react-responsive-carousel/lib/styles/carousel.min.css";

const ShopSection = (props) => {

  const { keyword, pagenumber } = props;

  const dispatch = useDispatch()

  const productList = useSelector((state) => state.productList);
  const {loading, error, products, page, pages} = productList

  useEffect(() => {
    dispatch(listProduct(keyword, pagenumber))
  },[ dispatch, keyword, pagenumber ]);
  return (
    <>
      <div className="container">
        <div className="section">
          <div className="carusel">
          <Carousel showArrows={true}>
            <div>
              <img src="https://www.elfbarvape.eu/!common_design/custom/elfbar.unas.hu/element/layout_en_banner_start_big-1920_5_default.jpg?time=1646850198"  alt="1" />
              <p className="legend">Elfbar</p>
            </div>
            <div>
              <img src="https://totally-vapour.co.uk/wp-content/uploads/2021/06/elf-bar-product-banner-1024x410.jpg" alt="2"/>
              <p className="legend">Choose your flavor</p>
            </div>
            <div>
              <img src="https://alishop.kiev.ua/image/cache/catalog/image/catalog/Abanner/banner.webp" alt="3" />
              <p className="legend">Elfbar 2000</p>
            </div>
          </Carousel>
          </div>
          <div className="row">
            <div className="col-lg-12 col-md-12 article">             
              <div className="shopcontainer row">
                {
                  loading ? (<div className="mb-5"><Loading/></div>) : error ? (<Message variant="alert-danger">{error}</Message>)
                  :
                  (
                    <>
                      {products.map((product) => (
                  <div
                    className="shop col-lg-4 col-md-6 col-sm-6"
                    key={product._id}
                  >
                    <div className="border-product">
                      <Link to={`/products/${product._id}`}>
                        <div className="shopBack">
                          <img src={product.image} alt={product.name} />
                        </div>
                      </Link>

                      <div className="shoptext">
                        <p>
                          <Link to={`/products/${product._id}`}>
                            {product.name}
                          </Link>
                        </p>

                        <Rating
                          value={product.rating}
                          text={`${product.numReviews} reviews`}
                        />
                        <h3>€{product.price}</h3>
                      </div>
                    </div>
                  </div>
                ))}
                    </>
                  )
                }
                
                {/* Pagination */}
                <Pagination pages={pages} page={page} keyword={keyword ? keyword : ""}/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ShopSection;
